*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

/* Main */

html {
  font-size: 14;
}

body {
  font-family: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

  background-color: var(--bg-color);
  color: var(--text-color);

  /* Variables */

  --bg-color: #ADD7F6;
  --text-color: #35281D; 
  --ally-color: #A1E887;
  --enemy-color: #F2545B;
}

.placeShip,
.gameBoard {
  width: 100%;
  padding: 0 6rem;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: 6rem auto 1fr;
  column-gap: 6rem;
}

.placeShip h1,
.gameBoard h1 {
  text-align: center;
  font-size: 4rem;
  line-height: 1.5em;
  grid-column: 1 / 3;
}

.boardSystem,
.ships {
  width: 42rem;
  display: grid;
  grid-template-columns: repeat(11, 1fr);
}

.ships.rotated {
  grid-auto-flow: column;
  grid-template-rows: repeat(9, 1fr);
}

.boardSystem:nth-child(3) {
  justify-self: start;
}

.boardSystem {
  grid-row: 2 / 4;
}

.cell {
  aspect-ratio: 1;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1em; 
}

.letters .cell,
.numbers .cell {
  display: flex;
  align-items: center;
  justify-content: center;
}

.letters {
  grid-row: 2 / span 10;
  display: grid;
  grid-template-rows: subgrid;
}

.numbers {
  grid-column: 2 / span 10;
  display: grid;
  grid-template-columns: subgrid;
}

.board {
  grid-row: 2 / span 10;
  grid-column: 2 / span 10;
  display: grid;
  grid-template-rows: subgrid;
  grid-template-columns: subgrid;
}

.board .cell {
   box-shadow: 
    2px 0 0 0 var(--text-color), 
    0 2px 0 0 var(--text-color), 
    2px 2px 0 0 var(--text-color),
    2px 0 0 0 var(--text-color) inset, 
    0 2px 0 0 var(--text-color) inset;
}

.board .cell.hovered {
  background-color: var(--ally-color);
  opacity: 0.8;
}

.board .cell.surrounding {
  background-color: var(--enemy-color);
  background-color: rgba(255, 31, 31, 0.4);
}

.cell.ship {
   box-shadow: 
    2px 0 0 0 var(--text-color), 
    0 2px 0 0 var(--text-color), 
    2px 2px 0 0 var(--text-color),
    2px 0 0 0 var(--text-color) inset, 
    0 2px 0 0 var(--text-color) inset;
  
}

.board.player .cell.ship,
.ships .cell.ship {
  background-color: var(--ally-color);
}

.board .cell.hit {
  background: repeating-linear-gradient(45deg, var(--bg-color),var(--bg-color) 2%, var(--text-color) 2%, var(--text-color) 4%);
}

.boardSystem .board.computer .cell.ship.hit {
  background: none;
  background-color: var(--enemy-color);
}

.board.player .cell.hit.ship {
  background: repeating-linear-gradient(-45deg, var(--ally-color),var(--ally-color) 2%, var(--text-color) 2%, var(--text-color) 4%);
}

.cell.ship.shadow {
   z-index: 999;
    position: absolute;
    filter: opacity(0.8);
    pointer-events: none;
}

.btns {
  display: flex;
  align-items: end;
  padding: 0 calc(42rem / 11);
  gap: 2.5rem;
}

.btns .btn {
  background-color: transparent;
  font-size: 2.4rem;
  font-weight: 700;
  line-height: 1.7em;
  width: 13rem;
  color: var(--text-color);
  border-radius: 1.5rem;
  border: 0.4rem solid var(--text-color);
  cursor: pointer;
  transition: 0.2s ease;
}

.btn:hover,
.btn:hover {
  background-color: var(--text-color);
  color: var(--bg-color);
}

.btns .btn.disabled {
  opacity: 0.75;
  pointer-events: none;
}


.blurContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.playAgain {
  width: 40rem;
  height: 24rem;
  background-color: var(--bg-color);
  border-radius: 1.5rem;
  text-align: center;
}

.playAgain h1 {
  font-size: 3.6rem;
  margin: 2rem 0 3rem;
}

.playAgain button {
  background-color: transparent;
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 2em;
  width: 24rem;
  color: var(--text-color);
  border-radius: 1.5rem;
  border: 0.4rem solid var(--text-color);
  cursor: pointer;
  transition: 0.2s ease;
